*{
    font-family: "Poppins", sans-serif;
}
body{
    height:100vh;
    width:100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color:rgba(248, 248, 160, 0.936);
    
}
button{
    color:black;
    cursor:grab;
    font-weight:  800;
    border:none;
    border-radius: 8px;
    padding:15px;
    background-color: rgb(187, 194, 45);
}
div{
    text-align: center;
}
b{
    background-color:rgba(255, 255, 255, 0.79);
    color:black;
    padding:5px;
    border-radius: 10px;

}
